import React, { useState, useEffect } from 'react'
import { Label, Input, FormGroup, Button } from 'reactstrap'
import { X } from 'react-feather'
import PerfectScrollbar from 'react-perfect-scrollbar'
import classnames from 'classnames'
import themeConfig from '../../configs/themeConfig'

const DataListSidebar = (props) => {
  const [relation, setrelation] = useState('Collegue')
  const [relation1, setrelation1] = useState('Collegue')
  const [email, setemail] = useState('')
  const [address, setaddress] = useState('')
  const [contact1, setcontact1] = useState('')
  const [contact2, setcontact2] = useState('')
  const [img, setimg] = useState('')
  const [id, setid] = useState('')
  const [name, setname] = useState('')
  const [addNew, setaddNew] = useState(false)
  const [errorrelation, seterrorrelation] = useState(false)
  const [erroremail, seterroremail] = useState(false)
  const [erroraddress, seterroraddress] = useState(false)
  const [errorcontact1, seterrorcontact1] = useState(false)
  const [errorcontact2, seterrorcontact2] = useState(false)
  const [errorname, seterrorname] = useState(false)

  useEffect(() => {
    console.log('ADD NEW NOMINEE', props.isLoading)
    if (props.data) {
      const {
        id,
        name,
        relation,
        address,
        email,
        primaryContact,
        secondaryContact
      } = props.data
      setid(id)
      setname(name)
      setrelation(relation)
      setrelation1(relation)
      setaddress(address)
      setemail(email)
      setcontact1(primaryContact)
      setcontact2(secondaryContact)
    } else if (props.data === null || addNew) {
      nullData()
    }
    setaddNew(false)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [props.data])

  const handleValidation = (type) => {
    switch (type) {
      case 'name':
        if (name.length === 0 && name === '') {
          seterrorname(true)
        } else {
          seterrorname(false)
        }
        break
      case 'email':
        if (email === '') {
          seterroremail(true)
        } else if (email !== '') {
          if (typeof email !== 'undefined') {
            if (
              !email.match(
                /^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,})$/
              )
            ) {
              seterroremail(true)
            } else {
              seterroremail(false)
            }
          }
        }
        break
      case 'address':
        if (!address) {
          seterroraddress(true)
        } else {
          seterroraddress(false)
        }
        break
      case 'contact1':
        if (contact1.length === 0) {
          seterrorcontact1(true)
        } else if (contact1.length !== 0) {
          if (typeof contact1 !== 'undefined') {
            if (contact1.length !== 10) {
              seterrorcontact1(true)
            } else {
              seterrorcontact1(false)
            }
          }
        }
        break
      case 'contact2':
        if (contact2.length === 0) {
          seterrorcontact2(false)
        } else if (contact2.length !== 0 && contact2.length === 10) {
          seterrorcontact2(false)
        } else {
          seterrorcontact2(true)
        }
        break
      case 'relation':
        if (relation === 'others' && relation1.length === 0) {
          seterrorrelation(true)
        } else {
          seterrorrelation(false)
        }
        break
      default:
        break
    }
  }

  const handleValue = (type, value) => {
    switch (type) {
      case 'name':
        setname(value)
        if (!value.length) {
          seterrorname('Enter Valid Name')
        } else {
          seterrorname('')
        }
        break
      case 'email':
        setemail(value)
        if (value === '') {
          seterroremail(true)
        } else if (value !== '') {
          if (typeof value !== 'undefined') {
            if (
              !value.match(
                /^([A-Za-z0-9_\-.])+@([A-Za-z0-9_\-.])+\.([A-Za-z]{2,})$/
              )
            ) {
              seterroremail(true)
            } else {
              seterroremail(false)
            }
          }
        }
        break
      case 'address':
        setaddress(value.replace(/[\n\r]/g, ', '))

        if (!value) {
          seterroraddress(true)
        } else {
          seterroraddress(false)
        }
        break
      case 'contact1':
        setcontact1(value)
        if (!value.length) {
          seterrorcontact1(true)
        } else if (value.length !== 0) {
          if (typeof value !== 'undefined') {
            if (value.length !== 10) {
              seterrorcontact1(true)
            } else {
              seterrorcontact1(false)
            }
          }
        }
        break
      case 'contact2':
        setcontact2(value)
        if (!value.length) {
          seterrorcontact2(false)
        } else if (value.length !== 0 && value.length === 10) {
          seterrorcontact2(false)
        } else {
          seterrorcontact2(true)
        }
        break
      case 'relation':
        setrelation(value)
        setrelation1(value)
        if (value !== 'others' && value.length === 0) {
          seterrorrelation(true)
        } else {
          seterrorrelation(false)
        }
        break
      case 'relation1':
        setrelation1(value)
        if (relation === 'others' && value.length === 0) {
          seterrorrelation(true)
        } else {
          seterrorrelation(false)
        }
        break
      default:
        break
    }
  }

  const resetError = (type) => {
    switch (type) {
      case 'name':
        seterrorname(false)
        break
      case 'email':
        seterroremail(false)
        break
      case 'address':
        seterroraddress(false)
        break
      case 'contact1':
        seterrorcontact1(false)
        break
      case 'contact2':
        seterrorcontact2(false)
        break
      case 'relation':
        seterrorrelation(false)
        break
      default:
        break
    }
  }

  const handleSubmit = () => {
    if (
      !(
        errorrelation ||
        errorcontact1 ||
        erroraddress ||
        errorcontact2 ||
        erroremail ||
        errorname
      )
    ) {
      const obj = {
        relation,
        email,
        address,
        contact1,
        contact2,
        id,
        name
      }
      if (props.data !== null) {
        obj._id = props.data._id
        obj.usage = props.data.usage
        props.updateData(obj)
      } else {
        setaddNew(true)
        props.addData(obj)
      }
    }
  }

  const nullData = () => {
    setname('')
    setrelation('')
    setrelation1('')
    setemail('')
    setaddress('')
    setcontact1('')
    setcontact2('')
    seterrorname(false)
    seterroraddress(false)
    seterrorcontact1(false)
    seterrorcontact2(false)
    seterroremail(false)
    seterrorrelation(false)
  }

  let { show, handleSidebar, data } = props
  return (
    <div
      className={classnames('data-list-sidebar', {
        show: show
      })}
    >
      <div className="data-list-sidebar-header mt-2 px-2 d-flex justify-content-between">
        <h4 style={{ color: 'var(--warning)' }}>
          {data !== null ? 'UPDATE NOMINEE' : 'ADD NEW NOMINEE'}
        </h4>
        <X
          size={20}
          onClick={() => {
            handleSidebar(false, true)
            nullData()
          }}
        />
      </div>
      <PerfectScrollbar
        className="data-list-fields px-2 pt-2"
        options={{ wheelPropagation: false }}
      >
        <FormGroup className="form-label-group mb-2">
          <Input
            className="input-label"
            type="text"
            value={name}
            placeholder="Nominee Name"
            onChange={(e) => {
              handleValue('name', e.target.value)
              resetError('name')
            }}
            onBlur={() => handleValidation('name')}
            id="data-name"
            style={{ borderColor: errorname ? 'red' : '' }}
          />
          {errorname && (
            <span
              className={
                themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
              }
            >
              {errorname}
            </span>
          )}
          <Label
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
            for="data-name"
          >
            Name
          </Label>
        </FormGroup>
        <FormGroup>
          <Input
            type="select"
            id="data-category"
            value={relation}
            onChange={(e) => {
              handleValue('relation', e.target.value)
            }}
          >
            <option>Collegue</option>
            <option>Cousin</option>
            <option>Daughter</option>
            <option>Daughter-in-law</option>
            <option>Friend</option>
            <option>Husband</option>
            <option>Son</option>
            <option>Son-in-law</option>
            <option>Wife</option>
            <option>others</option>
          </Input>
          <Label
            className={
              themeConfig.theme === 'dark'
                ? 'dark-label select-label'
                : 'light-label select-label'
            }
            for="data-category"
          >
            Relation
          </Label>
        </FormGroup>
        {relation === 'others' && (
          <FormGroup className="form-label-group mb-2">
            <Input
              type="text"
              value={relation1}
              placeholder="Relation Name"
              onChange={(e) => {
                handleValue('relation1', e.target.value)
                resetError('relation')
              }}
              onBlur={() => handleValidation('relation')}
              id="data-Relation"
              style={{ borderColor: errorrelation ? 'red' : '' }}
            />
          </FormGroup>
        )}
        <FormGroup className="form-label-group mb-2">
          <Input
            className="input-label"
            type="email"
            value={email}
            placeholder="Enter nominee email"
            onChange={(e) => {
              handleValue('email', e.target.value)
              resetError('email')
            }}
            onBlur={() => handleValidation('email')}
            id="data-email"
            style={{ borderColor: erroremail ? 'red' : '' }}
          />
          <Label
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
            for="data-email"
          >
            Nominee Email
          </Label>
        </FormGroup>
        <FormGroup className="form-label-group mb-2">
          <Input
            className="input-label"
            type="number"
            value={contact1}
            onChange={(e) => {
              handleValue('contact1', e.target.value)
              resetError('contact1')
            }}
            onBlur={() => handleValidation('contact1')}
            id="data-price"
            placeholder="Enter primary contact No"
            style={{ borderColor: errorcontact1 ? 'red' : '' }}
          />
          <Label
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
            for="data-price"
          >
            Primary Contact
          </Label>
        </FormGroup>
        <FormGroup className="form-label-group mb-2">
          <Input
            className="input-label"
            type="number"
            value={contact2}
            onChange={(e) => {
              handleValue('contact2', e.target.value)
              resetError('contact2')
            }}
            onBlur={() => handleValidation('contact2')}
            id="data-price"
            placeholder="Enter secondry contact No"
            style={{ borderColor: errorcontact2 ? 'red' : '' }}
          />
          <Label
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
            for="data-price"
          >
            Secondary Contact
          </Label>
        </FormGroup>
        <FormGroup className="form-label-group mb-2">
          <Input
            className="input-label "
            style={{
              height: '115px',
              borderColor: erroraddress ? 'red' : ''
            }}
            type="textarea"
            value={address}
            placeholder="Address"
            onChange={(e) => {
              handleValue('address', e.target.value)
              resetError('address')
            }}
            onBlur={() => handleValidation('address')}
            id="data-Address"
          />
          <Label
            className={
              themeConfig.theme === 'dark' ? 'dark-label' : 'light-label'
            }
            for="data-Address"
          >
            Address *
          </Label>
        </FormGroup>

        {props.thumbView && img.length <= 0 ? (
          <label
            className="btn btn-primary"
            htmlFor="upload-image"
            color="primary"
          >
            Upload Image
            <input
              type="file"
              id="upload-image"
              hidden
              onChange={(e) => setimg(URL.createObjectURL(e.target.files[0]))}
            />
          </label>
        ) : null}
      </PerfectScrollbar>
      <div className="data-list-sidebar-footer px-2 d-flex justify-content-start align-items-center mt-1">
        <Button.Ripple
          className="button-label"
          color="secondary"
          outline
          onClick={() => {
            nullData()
          }}
        >
          Reset
        </Button.Ripple>
        <Button.Ripple
          //disabled
          color="warning"
          type="reset"
          className="button-label"
          onClick={() => handleSubmit()}
        >
          {data !== null
            ? props.isLoading
              ? 'Updating...'
              : 'Update'
            : props.isLoading
            ? 'Adding...'
            : 'Add'}
        </Button.Ripple>
      </div>
    </div>
  )
}
export default DataListSidebar
