import React from 'react'
import { connect } from 'react-redux'
import { Row, Col, TabContent, TabPane } from 'reactstrap'
import AddLiability from './AddLiability'
import '../../assets/scss/plugins/extensions/dropzone.scss'
const Liability = (props) => {
  const { messages } = props

  return (
    <React.Fragment>
      <h2 className="warning spacing nodisplay">
        {messages?.heading ? messages.heading : 'Liabilties'}
      </h2>
      <Row>
        <Col sm="12">
          <TabContent activeTab={'1'}>
            <TabPane tabId="1">
              <AddLiability messages={messages} />
            </TabPane>
          </TabContent>
        </Col>
      </Row>
    </React.Fragment>
  )
}
const mapStateToProps = (state) => {
  const { customizer } = state?.customizer
  return {
    messages: customizer?.language?.Liability
  }
}

export default connect(mapStateToProps)(Liability)
